import React from "react";
import { Navigation, Pagination, Scrollbar, A11y,Autoplay } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
import {Routes,Route,Link,useNavigate,Outlet} from 'react-router-dom'

function Main(props){
    return(
        <section id='main'>
            <Swiper
            modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
            spaceBetween={0}
            slidesPerView={1}
            navigation
            autoplay={true}
            pagination={{ clickable: true }}
            scrollbar={{ draggable: true }}
            >
            {
                [1,2,3].map((a)=>{
                return(
                    <SwiperSlide><img src={`./img/slide-${a}.jpg`}/></SwiperSlide>
                )
                })
            }
            ...
            </Swiper>
        </section>
    )
}

function Section1(props){
    return(
        <section id='section1'>
            <div className='txt'>
            <h1 className='title'>About Pandora</h1>
            <p>     PANDORA   50       .</p>
            <p>            .</p>
            </div>
            <div className='box'>
            <div>
                {
                [1,2].map((a)=>{
                    return(
                    <img src={`./img/gallery-${a}.jpg`}/>
                    )
                })
                }
            </div>
            <div>
                {
                [3,4].map((a)=>{
                    return(
                    <img src={`./img/gallery-${a}.jpg`}/>
                    )
                })
                }
            </div>
            </div>
        </section>
    )
}

function Section2(props){
    let navigate = useNavigate()
    return(
        <section id='section2'>
            <div className='txt'>
            <h1 className='title'>New item</h1>
            <p>        .</p>
            <p>     .</p>
            </div>
            <div className='box'>
            {
                props.newdata.map((a,i)=>{
                return(
                    <div onClick={()=>{
                        navigate('/detail');
                        props.setData(props.newdata[i]);
                        props.setDatas(props.newdataDetail[i]);
                    }}>
                    <img className="bg" src={`${props.newdata[i].imgUrl}`}/>
                    <h1>{props.newdata[i].title}</h1>
                    <p>{props.newdata[i].content}</p>
                    <p className='line'>{props.Comma(props.newdata[i].Aprice)}</p>
                    <p className='price'>{props.Comma(props.newdata[i].price)}</p>
                    <br/>
                    <span className='pBox'>BEST</span><span className='pBox'>NEW</span>
                    </div>
                )
                })
            }
            </div>
        </section>
    )
}

