function Section4(props){
    let navigate = useNavigate()
    return(
        <section id='section4'>
            <div className='box'>
            <img src="./img/left_gallery.jpg"/>
            <div className='sBox'>
                <h1 className='title'>MD item</h1>
                <p>          .</p>
                <p>      .</p>
                <div className='sSlide'>
                <Swiper
                // install Swiper modules
                modules={[Navigation, Pagination, Scrollbar, A11y]}
                spaceBetween={0}
                slidesPerView={3}
                navigation
                pagination={{ clickable: true }}
                scrollbar={{ draggable: true }}
                >
                {
                    props.mddata.map((a,i)=>{
                    return(
                        <SwiperSlide
                        onClick={()=>{
                            navigate('/detail');
                            props.setData(props.mddata[i]);
                            props.setDatas(props.mddataDetail[i]);
                        }}>
                        <img className='bg' src={props.mddata[i].imgUrl}/>
                        <h1>{props.mddata[i].title}</h1>
                        <p>{props.mddata[i].content}</p>
                        <p className='line'>{props.Comma(props.mddata[i].Aprice)}</p>
                        <p className='price'>{props.Comma(props.mddata[i].price)}</p>
                        <br/>
                        <span className='pBox'>BEST</span><span className='pBox'>NEW</span>
                        </SwiperSlide>
                    )
                    })
                }
                ...
                </Swiper>
                </div>
            </div>
            </div>
        </section>
    )
}

