import React from "react";
import { Button,Nav,Navbar,Container,Row,Col,Table,Tabs,Tab } from 'react-bootstrap';
import { useState } from 'react';

function Detail(props){
  let [HData] = useState(props.data)
  let [BData] = useState(props.datas)
  let [check,setCheck] = useState(false)
  return(
    <div>
      <div id="detail_area">

        <Container fluid>
        <Row>
            <Col>
              <div className="detail_img">
                  <img src={HData.imgUrl}/>
              </div>
            </Col>
            <Col>
              <div className="detail_txt">
                <h1>{HData.title}</h1>
                <p className="price2">{props.Comma(HData.price)} <span>{props.Comma(HData.Aprice)}</span></p>
                <p className="content">{HData.content}</p>
                <ul>
                  <li><i class="fa-sharp-duotone fa-solid fa-star"></i></li>
                  <li><i class="fa-sharp-duotone fa-solid fa-star"></i></li>
                  <li><i class="fa-sharp-duotone fa-solid fa-star"></i></li>
                  <li><i class="fa-sharp-duotone fa-solid fa-star"></i></li>
                  <li><i class="fa-sharp-duotone fa-solid fa-star"></i></li>
                  <li>0.0 (0)</li>
                </ul>

                <div onClick={()=>{setCheck(!check)}} className="heart">
                  {check==false?<i class="fa-regular fa-heart"></i>:null}
                  {check==true?<i class="fa-solid fa-heart"></i>:null}
                </div>

                <div className="line_grey"></div>

                <Table className="delivery">
                <tbody>
                  <tr>
                    <th>  </th>
                    <td></td>
                  </tr>
                  <tr>
                    <th></th>
                    <td></td>
                  </tr>
                  <tr>
                    <th></th>
                    <td>3,000</td>
                  </tr>
                </tbody>
                </Table>

                <div className="line_grey"></div>

                <Table className="total">
                <tbody>
                  <tr>
                    <td> </td>
                    <th>{props.Comma(HData.price+3000)}</th>
                  </tr>
                </tbody>
                </Table>

                <Container className="detail_btn">
                <Row>
                  <Col><div className="btn_on">BUY NOW</div></Col>
                </Row>
                <Row>
                  <Col><div className="btn_off">CART</div></Col>
                  <Col><div className="btn_off">WISH</div></Col>
                </Row>
                </Container>
              </div>
            </Col>
        </Row>
        </Container>
        <Tabs
          defaultActiveKey="detail"
          id="fill-tab-example"
          className="mb-3"
          fill
        >
          <Tab eventKey="detail" title="" enabled>
            <img src={BData.imgUrl1}/>
            <img src={BData.imgUrl2}/>
          </Tab>
          <Tab eventKey="review" title="">
          <div id="review">
            <Container fluid>
              <Row>
                <Col>
                  <div className="review_title">
                    <h1></h1>
                    <div className="review_btn">
                      <div className="btn_off"></div>
                      <div className="btn_off"></div>
                    </div>
                  </div>
                </Col>
              </Row>

              <div className="line_grey"></div>

              <Row>
                <Col>
                  <div className="review_txt"> .</div>
                </Col>
              </Row>

              <div className="line_grey"></div>
            </Container>
          </div>
          </Tab>
          <Tab eventKey="inquiry" title="">
          <div id="review">
            <Container fluid>
              <Row>
                <Col>
                  <div className="review_title">
                    <h1></h1>
                    <div className="review_btn">
                      <div className="btn_off"></div>
                      <div className="btn_off"></div>
                    </div>
                  </div>
                </Col>
              </Row>

              <div className="line_grey"></div>

              <Row>
                <Col>
                  <div className="review_txt"> .</div>
                </Col>
              </Row>

              <div className="line_grey"></div>
            </Container>
          </div>
          </Tab>
          <Tab eventKey="delivery" title="">
          <div id="info">
            <Container fluid>
              <Row>
                <Col xs={12} md={6}>
                  <div className="info_area">
                    <div className="info_txt">
                      <h1></h1>
                      <p>        .                   .  
                      <br></br><br></br>
                           PC, ,       .  
                             , 7         .</p>
                    </div>

                    <div className="info_txt">
                      <h1></h1>
                      <p>  : <br></br>
                        : <br></br>
                        : 2,500<br></br>
                        : 3 ~ 7<br></br>
                        : -        .<br></br>
                            . ,        . </p>
                    </div>
                  </div>
                </Col>
                <Col xs={12} md={6}>
                <div className="info_area">
                    <div className="info_txt">
                      <h1>  </h1>

                      <h2>    </h2>
                      <p>-     7 , 
                              / .<br></br>
                      -      . 
                            3,     30
                      <br></br><br></br></p>

                      <h2>    </h2>
                      <p>-         . ,    
                            <br></br>
                      -       
                        ( : , ,  ,    , LCD,    
                         /   .)<br></br>
                      -            ,   
                           .<br></br>
                      -           <br></br>
                      -      <br></br>
                        (   1:1 E-MAIL   .)<br></br><br></br>

                          ,        .
                        ( ,    )
                        </p>
                    
                    </div>

                  </div>
                </Col>
              </Row>
            </Container>
          </div>
          </Tab>
        </Tabs>
      </div>
    </div>
  )
}

export default Detail;